<template>
    <div class="login_container">


        <div class="goBack" @click="$router.go(-1)">
            <div><i class="iconfont icon-fanhui"></i></div>
        </div>


        <header class="header">
            <h2 class="title">纽约外卖</h2>
        </header>


        <div class="login_title">
            <a href="javascript:" :class="{on: messageLogin}" @click="messageLogin=true">短信登录</a>
            <a href="javascript:" :class="{on: !messageLogin}" @click="messageLogin=false">账号登录</a>
        </div>

        <div class="login_content">
            <form action="" @submit.prevent="login">
                <div :class="{on:messageLogin}" class="loginWay_message">
                    <section class="login_input">
                        <input type="tel" maxlength="11" placeholder="手机号" v-model="phoneNumber">
                        <button id="btn" :disabled="!rightPhone" class="get_verification"
                                :class="{rightPhoneNumber:rightPhoneNumber}"
                                @click.prevent="getCode">
                            {{computerTime > 0 ? `已发送(${computerTime}s)`:'获取验证码'}}
                        </button>
                    </section>
                    <section class="login_verification">
                        <input type="tel" max-length="8" v-model="verifyCode" placeholder="请输入验证码">
                        <img src="./img/captcha.png" alt="" class="captcha">
                    </section>
                    <section class="login_prompt">
                        温馨提示：未注册纽约外卖帐号的手机号，登录时将自动注册，且代表已同意<a herf="#">《用户服务协议》</a> 。
                    </section>

                </div>
                <div :class="{on:!messageLogin}" class="loginWay_account">
                    <section class="login_input">
                        <input type="text" placeholder="手机/邮箱/微信" v-model="account" maxlength="18">
                        <input type="password" placeholder="密码" v-model="password">
                    </section>

                    <section class="login_prompt">
                        温馨提示：未注册硅谷外卖帐号的手机号，登录时将自动注册，且代表已同意<a herf="#">《用户服务协议》</a>
                    </section>
                </div>
                <div class="login_button">
                    <button>登 录</button>
                </div>
                <div class="about">
                    <a href="#">关于我们</a>
                </div>
            </form>
        </div>

        <AlertTip :alertText="alertText" v-show="alertShow" @closeTip="closeTip"/>


    </div>


</template>

<script>

    import alertTip from '../../components/AlertTip.vue'

	export default {
		name: 'login',
		data() {
			return {
				messageLogin: false, //登录方式：true为短信登录，false为账号密码登录
				phoneNumber: '',
				verifyCode: '',
				rightPhoneNumber: '',
				computerTime: 0,
				showPassword: true,
				account: '',
				password: '',
				alertText: '',
				alertShow: true
			}
		},
		computed: {
			rightPhone() {
				return /^1\d{10}$/.test(this.phoneNumber)
			}
		},

		methods: {
			getCode() {
				if (this.computerTime == 0) {
					this.computerTime = 30
                    document.getElementById('btn') .style.color = '#1010104D';
					const Count = setInterval(() => {
						this.computerTime = this.computerTime - 1
						if (this.computerTime <= 0) {
							clearInterval(Count)
						}
					}, 1000)
				}

			}


		},

		components:{
			'AlertTip':alertTip
        }


	}


</script>

<style scoped lang="less">

    .goBack > div {
        /*position: absolute;*/
        margin-top: 30px;
        margin-left: 30px;
        width: 30px;
        height: 30px;
        background-color: rgba(0, 0, 0, 0.1);
        border-radius: 50%;

        & {
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }

    .header {
        @height: 30vh;
        width: 100vw;
        position: relative;

        .title {
            color: #1296DB;
            text-align: center;
            line-height: 15vh;
            font-size: 2.5rem;

        }


    }

    .login_title {
        text-align: center;

        > a {
            color: #333;
            font-size: 14px;
            padding-bottom: 4px;;

            &:first-child {
                margin-right: 40px;
            }

            &.on {
                font-weight: 700;
                border-bottom: 2px solid #0086b3;
            }
        }
    }

    .login_content {
        margin: 25px auto;
        width: 300px;

        form {


            .loginWay_message {
                button {
                    height: 40px;
                    width: 100px;
                    position: relative;
                    bottom: 45px;
                    left: 65%;
                    background-color: #e3e9e9;
                    border: none;
                    border-radius: 5px;

                    & .rightPhoneNumber {
                        color: #000;
                    }
                }

                .captcha {
                    height: 35px;
                    display: inline-block;
                    position: relative;
                    top: -45px;
                    left: 225px
                }
            }

            .loginWay_account {
                button {
                    height: 40px;
                    width: 100px;
                    position: relative;
                    bottom: 45px;
                    left: 65%;
                    background-color: #e3e9e9;
                    border: none;
                    border-radius: 5px;

                    & .rightPhoneNumber {
                        color: #000;
                    }

                }

                input {
                    &:first-child {
                        margin-bottom: 4vh;
                    }

                }

            }

            div {
                display: none;

                &.on {
                    display: block;

                }
            }

            input {
                width: 100%;
                height: 50px;
                outline-color: #63a35c;
                font-size: 16px;
                border-radius: 5px;
                border-color: #c0c2c1;
            }


            .login_prompt {
                font-size: 12px;
                color: #c0c2c1;
                margin-top: 10px;

                a {
                    color: #63a35c;
                    text-decoration: underline;
                }
            }


        }

        .login_button {
            display: block;

            button {
                background-color: #1296DB;
                height: 50px;
                border-style: none;
                width: 100%;
                margin: 30px auto;
                color: #fff;
                font-size: 16px;
                border-radius: 5px;


            }
        }

        .about {
            width: 100%;
            height: 20px;
            color: #c0c2c1;
            display: block;
            text-align: center;
            font-size: 12px;
        }


    }


</style>



