import Vue from 'vue'
import Router from 'vue-router'
import index from '../pages/index.vue'
import order from '../pages/order.vue'
import profile from '../pages/profile.vue'
import search from '../pages/search.vue'
import breakfast from '../pages/gridPage/breakfast.vue'
import food from '../pages/gridPage/food.vue'
import dessert from '../pages/gridPage/dessert.vue'
import nightSnack from '../pages/gridPage/nightSnack.vue'
import fruit from '../pages/gridPage/fruit.vue'
import runOut from '../pages/gridPage/runOut.vue'
import membership from '../pages/gridPage/membership.vue'
import classify from '../pages/gridPage/classify.vue'
import login from '../pages/login/login.vue'
// import loginUnsigned from '../pages/login/loginUnsigned'


Vue.use(Router)


export default new Router({
	mode: 'history',
	routes: [{
			path: '/',
			redirect: '/index'
		}, {
			path: '/index',
			name: 'index',
			component: index,
			meta: {
				showTabBar: true
			}
		}, {
			path: '/login',
			name: 'login',
			component: login,
			meta: {
				showTabBar: false
			}
		},
		{
			path: '/order',
			name: 'order',
			component: order,
			meta: {
				showTabBar: true
			}
		},
		{
			path: '/profile',
			name: 'profile',
			component: profile,
			meta: {
				showTabBar: true
			}
		},
		{
			path: '/search',
			name: 'search',
			component: search,
			meta: {
				showTabBar: true
			}
		},
		{
			path: '/grid/breakfast',
			name: 'breakfast',
			component: breakfast,
			meta: {
				showTabBar: true
			}
		},
		{
			path: '/grid/food',
			name: 'food',
			component: food,
			meta: {
				showTabBar: true
			}
		},
		{
			path: '/grid/dessert',
			name: 'dessert',
			component: dessert,
			meta: {
				showTabBar: true
			}
		},
		{
			path: '/grid/nightSnack',
			name: 'nightSnack',
			component: nightSnack,
			meta: {
				showTabBar: true
			}
		},
		{
			path: '/grid/fruit',
			name: 'fruit',
			component: fruit,
			meta: {
				showTabBar: true
			}
		},
		{
			path: '/grid/runOut',
			name: 'runOut',
			component: runOut,
			meta: {
				showTabBar: true
			}
		},
		{
			path: '/grid/membership',
			name: 'membership',
			component: membership,
			meta: {
				showTabBar: true
			}
		},
		{
			path: '/grid/classify',
			name: 'classify',
			component: classify,
			meta: {
				showTabBar: true
			}
		}
	]
})



