<template>
	<div>
		<div class="grid">
			<ul>
				<li v-for="(module,index) in modules" :key="index">
					<router-link :to="module.route">
					<span :class="module.className"></span>
					<div>{{module.title}}</div>
					</router-link>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'grid',
		data() {
			return {
				modules: [
					{title:'早餐',className:'backgroundImage-breakfast',route:{name:'breakfast',	path:''}},
					{title:'美食',className:'backgroundImage-food',route:{name:'food'}},
					{title:'甜点',className:'backgroundImage-dessert',route:{name:'dessert'}},
					{title:'夜宵',className:'backgroundImage-nightSnack',route:{name:'nightSnack'}},
					{title:'水果',className:'backgroundImage-fruit',route:{name:'fruit'}},
					{title:'跑腿代购',className:'backgroundImage-runOut',route:{name:'runOut'}},
					{title:'会员',className:'backgroundImage-membership',route:{name:'membership'}},
					{title:'分类',className:'backgroundImage-classify',route:{name:'classify'}},
				]
			}
		},
		components: {
			
		},
		created() {
			
		}

	}
</script>

<style scoped lang="less">
	.grid {
		color:#424242;
	
		ul:after {
			content: '';
			visibility: none;
			display: block;
			clear: both;
		}

		ul {
			padding-left: 0;
			
		}

		margin: 2vh;
		padding-bottom: 2vh;
		border-radius: 2vh;
		background-color: #f7f7f7;

		li {
			float: left;
			width: 25%;
			text-align: center;
			
		}

		span {
			display: inline-block;
			margin: 10px 0;
			width: 50px;
			height: 50px;
			
		}
		
		
		.backgroundImage-breakfast{
			background-image: url(../../static/img/grid/breakfast.png);
			background-repeat:no-repeat;
			background-position: center;
			background-size:80% 80%;
			
		}
		
		.backgroundImage-food{
			background-image: url(../../static/img/grid/food.png);
			background-repeat:no-repeat;
			background-position: center;
			background-size:80% 80%;
			
		}
		
		.backgroundImage-dessert{
			background-image: url(../../static/img/grid/dessert.png);
			background-repeat:no-repeat;
			background-position: center;
			background-size:80% 80%;
		}
		
		.backgroundImage-nightSnack{
			background-image: url(../../static/img/grid/nightSnack.png);
			background-repeat:no-repeat;
			background-position: center;
			background-size:80% 80%;
		}
		
		.backgroundImage-fruit{
			background-image: url(../../static/img/grid/fruit.png);
			background-repeat:no-repeat;
			background-position: center;
			background-size:80% 80%;
		}
		
		.backgroundImage-runOut{
			background-image: url(../../static/img/grid/runOut.png);
			background-repeat:no-repeat;
			background-position: center;
			background-size:80% 80%;
		}
		
		.backgroundImage-membership{
			background-image: url(../../static/img/grid/membership.png);
			background-repeat:no-repeat;
			background-position: center;
			background-size:80% 80%;
		}
		
		.backgroundImage-classify{
			background-image: url(../../static/img/grid/classify.png);
			background-repeat:no-repeat;
			background-position: center;
			background-size:80% 80%;
		}

	}
</style>



