<template>
	<div class="FooterTabBar">
		<mt-tabbar fixed v-model="selected">
			<mt-tab-item id="index">
				<img slot="icon" src="../assets/index.png">
				首页
			</mt-tab-item>
			<mt-tab-item id="order">
				<img slot="icon" src="../assets/order.png">
				订单
			</mt-tab-item>
			<mt-tab-item id="search">
				<img slot="icon" src="../assets/search.png">
				搜索
			</mt-tab-item>
			<mt-tab-item id="profile">
				<img slot="icon" src="../assets/profile.png">
				我的
			</mt-tab-item>
		</mt-tabbar>

	</div>
</template>

<script>
	export default {
		name: 'FooterTabBar',
		data() {
			return {
				selected: ''
			}
		},
		watch: {
			selected(newV) {
				console.log(newV);
				this.$router.push({
					name: newV
				})

			}
		},

		props: [
			'index', 'order', 'search', 'profile',
		],
		methods: {

		}
	}
</script>

<style scoped lang="less">
	.mint-tab-item {
		background: #fafafa !important;
	}
</style>



