<template>
    <div id="index">
        <!--页头-->
        <div class="header">
            <router-link to="/search">
                <i class="iconfont icon-search-copy search"></i>
            </router-link>

            <img src="static/img/logo-ico.png" alt=""><span>纽约外卖</span>
        </div>

        <!--轮播图-->
        <mt-swipe :auto="4000">
            <mt-swipe-item v-for="(item,index) in imgs" :key="index">
                <img :src="item.imgSrc" alt="">
            </mt-swipe-item>
        </mt-swipe>

        <!--九宫格-->
        <grid></grid>

        <!--推荐列表-->
        <div class="shopItem">
            <div class="title"><i class="iconfont icon-entypomenu"></i>&nbsp;&nbsp;附近商家</div>
            <shopItem v-for="(item,index) in shopItems"
                      :shop-item-list="item.imgSrc"
                      :title="item.title"
                      :key="index"></shopItem>

        </div>


    </div>
</template>

<script>
	import grid from '../components/grid.vue'
	import shopItem from '../components/shopItem.vue'

	export default {
		name: "index",
		data() {
			return {
				imgs: [
					{id: 1, imgSrc: require('../assets/img/swipe-food-1.jpg')},
					{id: 2, imgSrc: require('../assets/img/swipe-food-2.jpg')},
					{id: 3, imgSrc: require('../assets/img/swipe-food-3.jpg')},
					{id: 4, imgSrc: require('../assets/img/swipe-food-4.jpg')},

				],
				shopItems: [
					{
						id: 1,
						imgSrc: '../static/img/thumbnail/1.jpg',
						title: '麦当劳',
						rank: ''
					}, {
						id: 2,
						imgSrc: '../static/img/thumbnail/2.jpg',
						title: '狗千岁香锅',
						rank: ''
					}, {
						id: 3,
						imgSrc: '../static/img/thumbnail/3.jpg',
						title: '必胜客',
						rank: ''
					}, {
						id: 4,
						imgSrc: '../static/img/thumbnail/4.jpg',
						title: '',
						rank: ''
					},

				]

			}
		},

		components: {
			grid,
			shopItem
		},

	}
</script>

<style lang="less" scoped>
    @header_height: 50px;

    #index {

        &::-webkit-scrollbar {
            display: none;
        }

    }

    .header {
        background-color: #1296DB;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: @header_height;
        z-index: 5000;
        text-align: center;
        vertical-align: middle;
        color: #fff;
        text-shadow: 2px 2px 5px #000;

        img {
            transform: translate(-15%, 25%);
            border-radius: 50%;

        }

        .search {
            position: absolute;
            top: 15px;
            left: 10px;
            display: inline-block;
            text-shadow: none;

            i {
                font-size: 20px;
            }
        }

    }

    .mint-swipe {
        height: 30vh;
        margin-top: @header_height;


    }

    .mint-swipe-item {
        width: 100%;
        height: 30vh;

        img {
            width: 100%;
            height: 100%;
        }
    }

    .shopItem {
        border-top: 1px solid #e8e8e8;
        margin-bottom: 55px ;
        .title{
            padding-top: 10px;
        }
    }


</style>



