<template>
    <div id="shopItem">
        <div class="container">
            <div class="shopItem_title">

            </div>
            <div class="shopItem_content">
                <div class="thumbnail">
                    <img :src="shopItemList" alt="">
                </div>
                <div class="contentBox">
                    <div class="title">
                        <span class="badge">品牌</span>{{title}}
                    </div>
                    <div class="rankAndSale">
                        ★★★★★
                    </div>
                    <div class="price">20元起送/配送费约5元</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
	export default {
		name: "ShopItem",
		props: ['shopItemList','title'],
		data() {
			return {
				shopItems: [
					{imgSrc: '', title: '',}
				]
			}
		}
	}
</script>

<style scoped lang="stylus">

    .clearfix:after
        display block
        visibility none
        clear both
        content ''
        float none

    .container
        margin-top 20px

    .shopItem_content

        width 100%

        &:after
            display block
            visibility none
            clear both
            content ''

        .thumbnail
            width 70px
            height 70px
            background-color #e8e8e8
            margin-left 10px
            float left
            img
                width 100%
                height 100%


        .contentBox
            width 50%
            float left
            margin-left 10px
            .badge
                background-color #ffe61b
                border-radius 5px
                padding 3px
                margin-right 10px
                color #666164


</style>



