<template>
	<div class="container">

		<div>
			<mt-header :title="title"></mt-header>
		</div>
		<router-link to="/login">
			<div class="profile_bar">
				<div class="avatar">
					<img src="../../static/img/profile_login.png" alt="">
				</div>
				<div class="profile_bar_item">
					<p class="title">登录/注册</p>
					<p class="phone"><i class="iconfont icon-shouji1"></i>   暂无绑定手机号</p>
					<i class="iconfont icon-arrowdropdown arrow"></i>

				</div>
			</div>
		</router-link>
		<div class="info">
			<ul>
				<li class="info_item_account">
					<span class="info_item_account_top"><span style="font-size: 1.25rem; font-weight: bold;color: darkorange;">0.00</span>元</span></br>
					<span class="info_item_account_bottom">我的余额</span>
				</li>
				<li class="info_item_discount">
					<span class="info_item_discount_top"><span style="font-size: 1.25rem; font-weight: bold;color: orange;">0</span>个</span></br>
					<span class="info_item_discount_bottom">我的优惠</span>
				</li>
				<li class="info_item_score">
					<span class="info_item_score_top"><span style="font-size: 1.25rem; font-weight: bold;color: seagreen;">0</span>分</span></br>
					<span class="info_item_score_bottom">我的积分</span>
				</li>
			</ul>
		</div>
		<div class="menu">
			<ul>
				<li class="order">我的订单</li>
				<li class="scoreShop">积分商城</li>
				<li class="membershipCard">纽约外卖会员卡</li>
				<li class="serviceCenter">服务中心</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		name: "profile",
		data() {
			return {
				title: '我的'
			}
		}
	}
</script>

<style scoped="scoped" lang="less">

	.container{
		background-color: #F7F7F7;
		width: 100%;
		height: 100%;
		overflow-y: hidden;
	}

	.mint-header {
		font-family: "微软雅黑 light";
		border-bottom: 1px #fff solid;
		font-size: 16px;
		color: #fff !important;
		background-color: #1296DB;

	}

	.profile_bar {
		width: 100%;
		height: 4.6875rem;
		background-color: #1296DB;
		.profile_bar_item{
			width: calc( 100% - 80px );
			height: 100%;
			transform: translateY(-18px);

			display: inline-block;
			position: relative;
			.title{
				font-weight: bold;
				font-size: 18px;
				color: #fff;
			}
			.phone{
				font-size: 12px;
				margin-top: 8px;
				color: #fff;
			}
			.arrow{
				color: white;
				display: block;
				position: absolute;
				right: 10px;
				top:20px;
			}
		}
	}

	.avatar{
		height: 75px;
		width: 75px;
		position: relative;
		display: inline-block;
		img{
			width: 75%;
			height: 75%;
			background-color: #fff;
			border-radius: 50% ;
			position: absolute;
			top: 50% - ( 75 / 2 ) ;
			left: 50% - ( 75 / 2 ) ;
		}
	}

	.info{
		ul{
			background-color: #fff;
			padding: 0.625rem 0;
			margin: 0.3125rem 0;

			&:after{
				content:'';
				display:block;
				visibility: none;
				clear: both;
			}
			li {
				width: ( 100% / 3 ) - 0.33px;
				float:left;
				border-right: 0.0625rem solid #e8e8e8;
			}
		}

		.info_item_account,
		.info_item_discount,
		.info_item_score{
			text-align: center;
		}

		.info_item_score{
			border-right:none;
		}
	}


	.menu{
		ul{
			margin:50px 0;
			li{
				height: 40px;
				background-color: #fff;
				width: 100%;
				padding:5px ;
				margin: 5px 0;
				line-height: 40px;
			}
		}
	}
</style>



